.inform {
  padding: 32px 18px 15px 18px;
  text-align: center;

  @media (min-width: $tablet) {
    padding: 45px 0 53px 0;
    max-width: 520px;
    margin: 0 auto;
  }

  @media (min-width: $desktop) {
    max-width: 484px;
    padding: 59px 0 44px 0;
  }
}

.inform__title {
  @include font-set(18px, 26px, 700);
  text-transform: uppercase;
  margin-bottom: 38px;
  padding: 0 10px;

  @media (min-width: $tablet) {
    @include font-set(21px, 26px, 700);
    margin-bottom: 30px;
  }
}

.inform__description {
  @include font-set(14px, 21px, 400);
  text-transform: uppercase;
  color: $black-lighter;
  margin: 0;
}
